import React, { useContext, useState, useEffect } from "react";
import { Link, NavLink, useNavigate } from "react-router-dom";
import { CartWidget } from "../CartWidget";
import { ArticulosContext } from "../context/providerArticulos";
import "react-toastify/dist/ReactToastify.css";
import { getFirestore, collection, getDocs } from "firebase/firestore";

export const Categorias = ({ cuerpo }) => {

  
  const navigate = useNavigate();
  const { setItems, items, datos, setDatos } = useContext(ArticulosContext);

  useEffect(() => {
    navigate("/trajedebao");
  }, []);

  const handleCategoriaClick = async (categoria) => {
    const db = getFirestore();
    const productosCollection = collection(db, "item");
    const productosSnapshot = await getDocs(productosCollection);
    const productosData = productosSnapshot.docs.map((doc) => ({
      id: doc.id,
      ...doc.data(),
    }));

    // Filtrar los productos por categora
    const productosFiltrados = productosData.filter(
      (producto) => producto.categoria === categoria
    );
    setDatos(productosFiltrados);

    navigate(`/${categoria}`);
  };




  useEffect(() => {
    handleCategoriaClick()
  })



  return (
    <div className="d-flex">
      <CartWidget articulos={items} setArticulos={setItems} />
      <div className="categorias d-flex pt-4">
        <button
          className={`btn categoria ${
            cuerpo === "trajedebao" ? "active" : ""
          }`}
          onClick={() => handleCategoriaClick("trajedebao")}
        >
          Trajes de bao
        </button>
        <button
          className="btn categoria"
          onClick={() => handleCategoriaClick("lenceria")}
        >
          Ropa interior
        </button>
      </div>
    </div>
  );
};
